*,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: #454f54;
  background-color: #f4f5f6;
  background-image: linear-gradient(to bottom left, #abb5ba, #d5dadd);
}

.ticket {
  display: grid;
  grid-template-rows: auto 1fr auto;
  max-width: 24rem;
}

.ticket__header,
.ticket__body,
.ticket__footer {
  padding: 1rem;
  background-color: #fff;
  border: 1px solid #abb5ba;
  box-shadow: 0 2px 4px rgba(41, 54, 61, 0.25);
}

.ticket__header {
  font-size: 1.3rem;
  border-top: 0.25rem solid #dc143c;
  border-bottom: none;
  box-shadow: none;
  padding: 20px 80px;
}

.ticket__wrapper {
  box-shadow: 0 2px 4px rgba(41, 54, 61, 0.25);
  border-radius: 0.375em 0.375em 0 0;
  overflow: hidden;
}

.ticket__divider {
  position: relative;
  height: 1rem;
  background-color: #fff;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.ticket__divider::after {
  content: "";
  position: absolute;
  height: 50%;
  width: 100%;
  top: 0;
  border-bottom: 2px dashed #e9ebed;
}

.ticket__notch {
  position: absolute;
  left: -0.5rem;
  width: 1rem;
  height: 1rem;
  overflow: hidden;
}

.ticket__notch::after {
  content: "";
  position: relative;
  display: block;
  width: 2rem;
  height: 2rem;
  right: 100%;
  top: -50%;
  border: 0.5rem solid #fff;
  border-radius: 50%;
  box-shadow: inset 0 2px 4px rgba(41, 54, 61, 0.25);
}

.ticket__notch--right {
  left: auto;
  right: -0.5rem;
}

.ticket__notch--right::after {
  right: 0;
}

.ticket__body {
  border-bottom: none;
  border-top: none;
}

.ticket__body > * + * {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e9ebed;
}

.ticket__section > * + * {
  margin-top: 0.25rem;
}

.ticket__section > h3 {
  font-size: 1.125rem;
  margin-bottom: 0.5rem;
}

.ticket__header {
  font-weight: bold;
  font-size: 1.25rem;
}

.ticket__footer {
  border-top: 2px dashed #e9ebed;
  border-radius: 0 0 0.325rem 0.325rem;
  font-size: 1.25rem;
}

.seatNo {
  display: inline-block;
}

.names {
  text-transform: capitalize;
}

.idData {
  font-size: 15px;
  text-transform: uppercase;
}

.ticket-qr {
  height: 250px;
}

body {
    background: url(/static/media/bg.4c7785ea.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
    height: 100%;
    width: 100%;
}

.page{
    position: absolute;
}


.fade-appear, .fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-appear-active, .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms linear 150ms;
}

.fade-exit{
    opacity: 1;
}

.fade-exit.fade-exit-active{
    opacity: 1;
    transition: opacity 150ms linear;

}
.main-container {
  display: flex;
  flex-direction: column;
}

.mainBtnDash {
  position: relative;
  left: 74%;
  display: inline-block;
  margin: 1%;
  width: 277px;
  height: 50px;
  font-size: 1em;
  font-weight: bold;
  line-height: 60px;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-size: 17px;
  letter-spacing: 0.045em;
}

.mainBtnDash svg {
  position: absolute;
  top: 0;
  left: 0;
}

.mainBtnDash svg rect {
  /* stroke: #EC0033; */
  stroke-width: 4;
  stroke-dasharray: 353, 0;
  stroke-dashoffset: 0;
  transition: all 600ms ease;
}

.mainBtnDash span {
  background: rgb(130, 230, 255);
  background: linear-gradient(
    to right,
    rgba(130, 230, 255, 1) 0%,
    rgb(130, 230, 255) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8282', endColorstr='#e178ed', GradientType=1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.mainBtnDash:hover svg rect {
  stroke-width: 4;
  stroke-dasharray: 196, 543;
  stroke-dashoffset: 437;
}

.mapImg {
  display: flex;
  justify-content: flex-end;
  margin: 1%;
}

.monthsGraph {
  margin-top: 10%;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}

.ticketBox {
  padding: 50px;
  background-color: rgb(21, 19, 22);
  color: white;
  border-radius: 5%;
  box-shadow: white;
}

